nav {
  font-size: 1.3rem;
  font-weight: 400;
  color: #cec8c4;
  z-index: 100;
  position: fixed;
  top: 0rem;
  left: 0;
  display: block;
  background: linear-gradient(
    112.1deg,
    rgb(32, 38, 57) 11.4%,
    rgb(63, 76, 119) 70.2%
  );
  height: 100vh;
  overflow: hidden;
  transition: width 0.3s ease-in-out;
}
nav ul {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  padding-inline: 0.8rem 1rem;
  height: 95%;
  overflow: visible;
}
nav li {
  display: inline;
  margin-block: 0.5rem;
  cursor: pointer;
  width: max-content;
  display: inline-flex;
  gap: 1rem;
  opacity: 0.8;
  overflow: visible;
  padding-block: 0.5rem;
  margin-block: 0;
}
.span {
  display: inline-block;
  margin-left: 1rem;
}
nav li:first-of-type ion-icon:first-of-type {
  transform: scale(1.2);
}
nav li:hover {
  opacity: 1;
}

nav ion-icon {
  vertical-align: baseline;
}

.navButton {
  text-align: right;
  justify-content: end;
}

.navToggle {
  width: 100%;
  margin: 0;
}
#menu {
  margin-top: 1.5rem;
  padding-bottom: 15vh;
  position: relative;
}

#linkedIn {
  margin-top: auto;
}

/* .navHidden {
  width: 3rem;
  transition: width 0.5s ease-in-out;
} */
.navHidden ul {
  padding: 0;
}
.navHidden ul span {
  display: none;
}
.navHidden .navButton {
  width: 100%;
}
.navHidden .navButton {
  text-align: right;
  justify-content: center;
}

@media screen and (max-width: 1200px) {
  nav ul {
    height: 90%;
  }
}

li a {
  text-decoration: none;
  color: inherit;
  padding-block: 0.35rem;
}
li a:hover,
li a:visited {
  color: #cec8c4;
}
